@use "global" as *;

.nav__items{
  display: flex;
  z-index: 10;
  color: $white;
  font-weight: $regular;
  padding-top: rem(64);
  z-index: 15;
}

.nav_item a{
  display: block;
}

.nav__items--left {
  width: 50%;
  font-family: $base-font-family;
  font-size: rem(16);
  color: $white;
  height: 100vh;
  z-index: 15;
}
.nav__items--right {
  width: 50%;
  font-family: $base-font-family;
  font-size: rem(16);
  color: $white;
  height: 100vh;
  
}



.nav__item{
  padding-top: rem(24);
  padding-left: rem(37);
}

.nav__item--bold {
  font-weight: $bold;
  padding-top: rem(40);
  padding-left: rem(37);
  @include mq (md){
    // padding-top: rem(25);
  }
}

.nav__item--bottom{
  z-index: 10;
  color: $white;
  font-weight: $regular;
  @include mq (md) {
    // justify-content: space-around;
    // margin: auto;
  }
}
.nav__bottom--icons{
  display: flex;
  flex-direction: column;
}

.nav__items--left--bottom{
  width: 50%;
  font-family: $base-font-family;
  font-size: rem(16);
  color: $white;
}
.nav__item--right--bottom{
  width: 50%;
  font-family: $base-font-family;
  font-size: rem(16);
  color: $white;
}